import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/Home'
import Vendor from '@/components/Vendor'
import Login from '@/components/Login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: HelloWorld
    },
    {
      path: '/vendor',
      name: 'Vendor',
      component: Vendor
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ]
})



